.App {
    width: 100vw;
    height: 100svh;
    text-align: center;
    background-color: rgb(32, 32, 32);
}

.Main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.margin {
    height: 40px
}

.test_starter{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 60px;
    border: 2px solid white;
    border-radius: 10px;
    margin-top: 20px;
    color: white;
    font-size: 20px;
}

.Test {
    display: flex;
    flex-direction: column;
}

.appbar {
    display: flex;
    flex-direction: row;
    width: 100vw;
    height: 60px;
    color: aliceblue;
}

.appbar_side {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 60px;
}

.test_name {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: calc(100vw - 160px);
    height: 60px;
    font-size: 18px;
    font-weight: bold;
}

.content {
    width: 100vw;
    height: calc(100svh - 122px);
}

.question {
    white-space: pre-wrap;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: calc(100vw - 84px);
    height: calc(10svh - 27px);
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 20px;
    padding: 20px;
    border: 2px solid white;
    border-radius: 10px;
    font-size: 16px;
    color: white;
}

.answer {
    white-space: pre-wrap;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: calc(100vw - 84px);
    height: calc(90svh - 243px);
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 20px;
    padding: 20px;
    border: 2px solid white;
    border-radius: 10px;
    font-size: 14px;
    color: white;
}

.bottom {
    display: flex;
    flex-direction: row;
    color: aliceblue;
    border-top: 1px solid white;
    height: 61px
}

.bottom_side {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 61px;
    font-size: 18px;
}

.see_answer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: calc(100vw - 240px);
    height: 61px;
    font-size: 20px;
    font-weight: bold;
}